"use client"

import { useState } from "react"
import Navigation from "@/components/navigation"
import SpacesPanel from "@/components/spaces-panel"
import ItemsPanel from "@/components/items-panel"
import ViewportCanvas from "@/components/viewport-canvas"
import ViewControls from "@/components/view-controls"
import ShipmentPanel from "@/components/shipment-panel"
import { Button } from "@/components/ui/button"

type Tab = "spaces" | "items" | "backups" | "shipments"

export interface PlacedBox {
  id: string
  itemId: string
  position: [number, number, number]
  dimensions: [number, number, number]
  color: string
  name: string
}

export default function CargoLoaderApp() {
  const [activeTab, setActiveTab] = useState<Tab>("spaces")
  const [sidebarOpen, setSidebarOpen] = useState(true)
  const [selectedContainer, setSelectedContainer] = useState<string | null>(null)
  const [items, setItems] = useState<any[]>([])
  const [selectedItemId, setSelectedItemId] = useState<string | null>(null)
  const [placedBoxes, setPlacedBoxes] = useState<PlacedBox[]>([])

  const handlePlaceBox = (position: [number, number, number]) => {
    if (!selectedItemId) return

    const selectedItem = items.find((item) => item.id === selectedItemId)
    if (!selectedItem) return

    // Create new box with gravity-adjusted position
    const dimensions: [number, number, number] = [
      (selectedItem.length || 100) / 100,
      (selectedItem.height || 100) / 100,
      (selectedItem.width || 100) / 100,
    ]

    const adjustedPosition = applyGravityAndCollision(position, dimensions, placedBoxes)

    if (adjustedPosition) {
      const newBox: PlacedBox = {
        id: `${selectedItemId}-${Date.now()}`,
        itemId: selectedItemId,
        position: adjustedPosition,
        dimensions,
        color: selectedItem.color,
        name: selectedItem.name,
      }
      setPlacedBoxes([...placedBoxes, newBox])
    }
  }

  const handleSelectPlacedBox = (boxId: string) => {
    console.log("[v0] Selected placed box:", boxId)
    // You can add highlighting logic here
  }

  return (
    <div className="flex h-screen flex-col bg-gray-50">
      <Navigation activeTab={activeTab} onTabChange={setActiveTab} />

      <div className="flex flex-1 overflow-hidden">
        {/* Left Sidebar */}
        {sidebarOpen && (
          <aside className="w-80 border-r border-gray-200 bg-white">
            {activeTab === "spaces" && (
              <SpacesPanel
                onToggle={() => setSidebarOpen(!sidebarOpen)}
                selectedContainer={selectedContainer}
                onSelectContainer={setSelectedContainer}
              />
            )}
            {activeTab === "items" && (
              <ItemsPanel
                onToggle={() => setSidebarOpen(!sidebarOpen)}
                items={items}
                onItemsChange={setItems}
                selectedItemId={selectedItemId}
                onSelectItem={setSelectedItemId}
              />
            )}
          </aside>
        )}

        {/* Main Content Area */}
        <main className="relative flex flex-1 flex-col">
          {/* 3D Viewport */}
          <div className="flex-1 bg-gray-100">
            <ViewportCanvas
              container={selectedContainer}
              items={items}
              placedBoxes={placedBoxes}
              selectedItemId={selectedItemId}
              onPlaceBox={handlePlaceBox}
              onSelectBox={handleSelectPlacedBox}
            />
          </div>

          {/* Bottom Shipment Panel */}
          <ShipmentPanel container={selectedContainer} />

          {/* View Controls */}
          <ViewControls />

          {/* Load Button */}
          <Button
            size="lg"
            className="absolute bottom-32 right-8 h-24 w-24 rounded-full bg-blue-400 text-2xl font-semibold text-white shadow-lg hover:bg-blue-500"
          >
            Load
          </Button>
        </main>
      </div>
    </div>
  )
}

function applyGravityAndCollision(
  targetPosition: [number, number, number],
  dimensions: [number, number, number],
  existingBoxes: PlacedBox[],
): [number, number, number] | null {
  const [x, y, z] = targetPosition
  const [width, height, depth] = dimensions

  // Container bounds (matching container dimensions)
  const containerWidth = 12
  const containerHeight = 2.4
  const containerDepth = 2.4
  const containerFloor = -containerHeight / 2

  if (Math.abs(x) > containerWidth / 2 - width / 2 || Math.abs(z) > containerDepth / 2 - depth / 2) {
    return null
  }

  // Apply gravity: find the lowest valid Y position
  let finalY = containerFloor + height / 2 // Start at floor

  // Check collisions with existing boxes
  for (const box of existingBoxes) {
    const [bx, by, bz] = box.position
    const [bw, bh, bd] = box.dimensions

    // Check if boxes overlap in X-Z plane
    const overlapX = Math.abs(x - bx) < (width + bw) / 2
    const overlapZ = Math.abs(z - bz) < (depth + bd) / 2

    if (overlapX && overlapZ) {
      // Boxes overlap in X-Z, so new box must be above this one
      const topOfBox = by + bh / 2 + height / 2
      finalY = Math.max(finalY, topOfBox)
    }
  }

  // Check if final position is within container height
  if (finalY + height / 2 > containerHeight / 2) {
    return null
  }

  return [x, finalY, z]
}
