"use client"

import { useRef, useState } from "react"
import * as THREE from "three"
import { useFrame } from "@react-three/fiber"
import type { ThreeEvent } from "@react-three/fiber"
import type { PlacedBox } from "@/components/cargo-loader-app"

interface Container3DProps {
  items: any[]
  placedBoxes: PlacedBox[]
  selectedItemId: string | null
  onPlaceBox: (position: [number, number, number]) => void
  onSelectBox: (boxId: string) => void
}

export default function Container3D({ items, placedBoxes, selectedItemId, onPlaceBox, onSelectBox }: Container3DProps) {
  const containerRef = useRef<THREE.Group>(null)
  const [hoveredBox, setHoveredBox] = useState<string | null>(null)
  const [ghostPosition, setGhostPosition] = useState<[number, number, number] | null>(null)
  const [ghostIsValid, setGhostIsValid] = useState<boolean>(false)

  // Container dimensions (scaled down for visualization)
  const containerWidth = 12
  const containerHeight = 2.4
  const containerDepth = 2.4

  const selectedItem = selectedItemId ? items.find((item) => item.id === selectedItemId) : null
  const ghostDimensions: [number, number, number] = selectedItem
    ? [(selectedItem.length || 100) / 100, (selectedItem.height || 100) / 100, (selectedItem.width || 100) / 100]
    : [1, 1, 1]

  const handleContainerHover = (event: ThreeEvent<PointerEvent>) => {
    if (!selectedItemId) return

    event.stopPropagation()
    const point = event.point

    // Find nearest valid position
    const validPosition = findNearestValidPosition(
      [point.x, point.y, point.z],
      ghostDimensions,
      placedBoxes,
      containerWidth,
      containerHeight,
      containerDepth,
    )

    if (validPosition) {
      setGhostPosition(validPosition.position)
      setGhostIsValid(validPosition.isValid)
    }
  }

  const handleContainerLeave = () => {
    setGhostPosition(null)
  }

  const handleContainerClick = (event: ThreeEvent<MouseEvent>) => {
    if (!selectedItemId || !ghostPosition) return

    event.stopPropagation()
    onPlaceBox(ghostPosition)
  }

  return (
    <group ref={containerRef} position={[0, containerHeight / 2, 0]}>
      {/* Container Frame (wireframe) */}
      <lineSegments>
        <edgesGeometry args={[new THREE.BoxGeometry(containerWidth, containerHeight, containerDepth)]} />
        <lineBasicMaterial color="#666666" linewidth={2} />
      </lineSegments>

      {/* Container Walls (semi-transparent, clickable) */}
      <mesh
        position={[0, 0, 0]}
        onClick={handleContainerClick}
        onPointerMove={handleContainerHover}
        onPointerLeave={handleContainerLeave}
      >
        <boxGeometry args={[containerWidth, containerHeight, containerDepth]} />
        <meshPhongMaterial
          color={selectedItemId ? "#e8f4fd" : "#e0e0e0"}
          transparent
          opacity={0.15}
          side={THREE.DoubleSide}
        />
      </mesh>

      {/* Floor Platform */}
      <mesh position={[0, -containerHeight / 2 - 0.1, 0]} receiveShadow>
        <boxGeometry args={[containerWidth + 0.5, 0.2, containerDepth + 1]} />
        <meshStandardMaterial color="#555555" metalness={0.6} roughness={0.4} />
      </mesh>

      {/* Wheels */}
      <group position={[0, -containerHeight / 2 - 0.3, containerDepth / 2 + 0.3]}>
        <Wheel position={[-containerWidth / 2 + 1, 0, 0]} />
        <Wheel position={[-containerWidth / 2 + 2.5, 0, 0]} />
        <Wheel position={[containerWidth / 2 - 2.5, 0, 0]} />
        <Wheel position={[containerWidth / 2 - 1, 0, 0]} />
      </group>

      {ghostPosition && selectedItemId && (
        <GhostBox position={ghostPosition} dimensions={ghostDimensions} isValid={ghostIsValid} />
      )}

      {placedBoxes.map((box) => (
        <InteractiveBox
          key={box.id}
          box={box}
          isHovered={hoveredBox === box.id}
          onHover={() => setHoveredBox(box.id)}
          onUnhover={() => setHoveredBox(null)}
          onClick={() => {
            onSelectBox(box.id)
          }}
        />
      ))}
    </group>
  )
}

function GhostBox({
  position,
  dimensions,
  isValid,
}: {
  position: [number, number, number]
  dimensions: [number, number, number]
  isValid: boolean
}) {
  const meshRef = useRef<THREE.Mesh>(null)

  // Subtle pulsing animation
  useFrame(({ clock }) => {
    if (meshRef.current) {
      const scale = 1 + Math.sin(clock.getElapsedTime() * 3) * 0.02
      meshRef.current.scale.set(scale, scale, scale)
    }
  })

  const color = isValid ? "#22c55e" : "#ef4444" // green if valid, red if invalid

  return (
    <mesh ref={meshRef} position={position}>
      <boxGeometry args={dimensions} />
      <meshStandardMaterial color={color} transparent opacity={0.4} wireframe={false} />
      {/* Wireframe outline */}
      <lineSegments>
        <edgesGeometry args={[new THREE.BoxGeometry(...dimensions)]} />
        <lineBasicMaterial color={color} linewidth={3} />
      </lineSegments>
    </mesh>
  )
}

function findNearestValidPosition(
  targetPosition: [number, number, number],
  dimensions: [number, number, number],
  existingBoxes: PlacedBox[],
  containerWidth: number,
  containerHeight: number,
  containerDepth: number,
): { position: [number, number, number]; isValid: boolean } | null {
  const [targetX, targetY, targetZ] = targetPosition
  const [width, height, depth] = dimensions
  const containerFloor = -containerHeight / 2

  // Clamp X and Z to container bounds
  const x = Math.max(-containerWidth / 2 + width / 2, Math.min(containerWidth / 2 - width / 2, targetX))
  const z = Math.max(-containerDepth / 2 + depth / 2, Math.min(containerDepth / 2 - depth / 2, targetZ))

  // Apply gravity: find the lowest valid Y position
  let y = containerFloor + height / 2 // Start at floor

  // Check collisions with existing boxes and stack on top if needed
  for (const box of existingBoxes) {
    const [bx, by, bz] = box.position
    const [bw, bh, bd] = box.dimensions

    // Check if boxes overlap in X-Z plane
    const overlapX = Math.abs(x - bx) < (width + bw) / 2
    const overlapZ = Math.abs(z - bz) < (depth + bd) / 2

    if (overlapX && overlapZ) {
      // Boxes overlap in X-Z, so new box must be above this one
      const topOfBox = by + bh / 2 + height / 2
      y = Math.max(y, topOfBox)
    }
  }

  // Check if final position is within container height
  const isValid = y + height / 2 <= containerHeight / 2

  return {
    position: [x, y, z],
    isValid,
  }
}

function InteractiveBox({
  box,
  isHovered,
  onHover,
  onUnhover,
  onClick,
}: {
  box: PlacedBox
  isHovered: boolean
  onHover: () => void
  onUnhover: () => void
  onClick: () => void
}) {
  return (
    <mesh
      position={box.position}
      castShadow
      onClick={(e) => {
        e.stopPropagation()
        onClick()
      }}
      onPointerOver={(e) => {
        e.stopPropagation()
        onHover()
      }}
      onPointerOut={(e) => {
        e.stopPropagation()
        onUnhover()
      }}
    >
      <boxGeometry args={box.dimensions} />
      <meshStandardMaterial
        color={box.color}
        metalness={0.1}
        roughness={0.5}
        emissive={isHovered ? box.color : "#000000"}
        emissiveIntensity={isHovered ? 0.3 : 0}
      />
      {/* Selection outline */}
      {isHovered && (
        <lineSegments>
          <edgesGeometry args={[new THREE.BoxGeometry(...box.dimensions)]} />
          <lineBasicMaterial color="#ffffff" linewidth={2} />
        </lineSegments>
      )}
    </mesh>
  )
}

function Wheel({ position }: { position: [number, number, number] }) {
  return (
    <mesh position={position} rotation={[0, 0, Math.PI / 2]} castShadow>
      <cylinderGeometry args={[0.3, 0.3, 0.4, 16]} />
      <meshStandardMaterial color="#222222" metalness={0.8} roughness={0.3} />
    </mesh>
  )
}
